import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';
import { BackProvider } from '../../providers/back/back';

@IonicPage()
@Component({
  selector: 'page-simulador-detail',
  templateUrl: 'simulador-detail.html',
})
export class SimuladorDetailPage {

  private id: string = this.navParams.get('id')
  public s: any;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private back: BackProvider,
    private toast: ToastController
  ) {
  }

  ionViewDidLoad() {
    this.back.getSimuladorDetail(this.id).subscribe(data => this.s = data, err => {
      this.toast.create({message: 'No se pudo cargar el simulador...', duration: 2000}).present()
      if (this.navCtrl.canGoBack()) this.navCtrl.pop()
    })
  }

}



